import { Controller } from "stimulus"

export default class extends Controller {
  static targets = ['card', 'error', 'source', 'form']

  connect () {
    this.init()
  }

  init () {
    if (Stripe) {
      this.stripe = Stripe(this.element.dataset.stripeKey)
      let elements = this.stripe.elements()

      this.card = elements.create('card')
      this.card.mount(this.cardTarget)
      this.card.addEventListener('change', (event) => this.stripeChange(event))
    }
    else {
      setTimeout(() => this.init(), 100)
    }
  }

  submit (e) {
    console.log('submit')
    if (!this.sourceTarget.value) {
      e.preventDefault()
      console.log('source empty')
      this.stripe.createSource(this.card).then((result) => this.stripeCreateSoruce(result))
    }
  }

  stripeCreateSoruce (result) {
    console.log('stripeCreateSoruce')
    if (result.error) {
      this.errorTarget.textContent = result.error.message
    }
    else {
      this.sourceTarget.value = result.source.id
      this.formTarget.submit()
    }
  }

  stripeChange (event) {
    this.sourceTarget.value = null

    if (event.error) {
      this.errorTarget.textContent = event.error.message;
    } else {
      this.errorTarget.textContent = '';
    }
  }

}



